@import url("https://fonts.googleapis.com/css?family=Fauna+One|Oxygen:400,300,700");
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-size: 14px;
  line-height: 1.42857143;
  margin: 0;
}

img {
  vertical-align: middle;
}

.NoteForm {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 0 3rem;
}
.NoteForm .form-actions {
  padding-top: 1rem;
  margin-left: -2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
}
.NoteForm button {
  border: none;
  background: none;
  padding: 0;
}

.NoteForm button[type="submit"] {
  background-color: #008BF8;
  border-radius: 4px;
  color: white;
  font-size: 1.4rem;
  padding: 1rem;
}


.NoteForm button i.fa {
  color: #999;
  font-size: 2rem;
  margin: 0;
}
.NoteForm form {
  margin: 0 auto;
  max-width: 80rem;
}
input[name="title"] {
  border: none;
  font-size: 200%;
  font-family: "Fauna One";
  color: #008BF8;
  font-weight: 400;
  width: 100%;
}
input[name="title"] {
  outline: none;
}
textarea {
  border-color: #eee;
  width: 100%;
  height: calc(100vh - 140px);
  font-size: 1.3em;
}



